


body {
  background-color: #b5f1fc; 

}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}


*:focus {
  outline: none;
}

.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 5px 20px;
  align-content: center;
  align-items: center;
  align-self: center;
  font-size: 14px;
  line-height: 20px;
}


h1 {
  text-align: center;
  font-size: 22px;
  margin-top: 20px;
  margin-bottom: 0px;
}

h4 {
  text-align: center;
  font-size: 15px;
}



footer {
  position: fixed;
  bottom: 0px;
  height: 40px;
  width: 100%;
  background-color: white;
  color: #51B8F9;
  font-size: 15px;
  display: flex;
  /* border-top: 1px solid grey; */
  align-items: center;
  justify-content: center;
}

.Navbar {
  position: fixed;
  top: 0px;
}

input {
  margin: 0px;
  padding: 0px;
  font-size: 13px;
  font-style: italic;
}

p {
  margin: 3px 0px;
}

.button {
  background-color: #51B8F9;
  font-size: 11px;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  border-color: #0079c4;
  margin: 10px 3px;
  padding: 7px;
  width: 90px;
  min-height: 30px;
}

.button:hover {
  background-color: #1298eb;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.26);

}


.disabled {
  background-color: rgb(165, 165, 165);
  border-color: #5c5e69;
}

.disabled:hover {
  background-color: rgb(165, 165, 165);
  border-color: #5c5e69;
  box-shadow: none;

}


.main-content {
  margin-top: 20px;
  width: 100%;
  max-width: 500px;
  min-width: 300px;
}


.sign-log-form {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 20px 0px;
  max-width: 300px;
  min-width: 300px;
}

.login-label {
  margin: 8px;
  font-weight: bold;
}

.label-user {
  font-size: 13px;
}

.home-doctor-card {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.26);
  margin: 10px 0px;
  border: 2px solid #51B8F9;
  border-radius: 10px;
  background-color: white;
}

.home-doctor-card > * {
  padding: 8px;
}


.search-doc-details-text {
  max-width: 180px;
}

.index-info-card {
  display: flex;
  justify-content: space-between ;
  width: 100%;
  max-width: 550px;
  justify-self: center;
}

.index-info-card img {
  width: 170px;
}


.index-img-card {
  max-width: 200px;
  width: 30%;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.hidden-button{
  display: none
}


.appointment-card {
  padding: 5px;
  border-radius: 10px;
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.doc-img {
  width: 80px;
  min-height: 80px;  
  object-fit: cover;
  border-radius: 50%;
}

.edit-profile {
  background-color: transparent;
  border-color: transparent;
}


.img-edit-btn-profile {
  width: 18px;
  margin: 0px;
}


.home-search-bar {
  margin: 40px 0px 0px 0px;
}


.input-search {
  height: 20px;
}



/* DOCTOR / PATIENT PROFILE */

.profile-field {
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
}

.readonly-field{
  background-color:transparent;
  border: none;
  /* padding: 4px; */
  outline:none;
  width: 60%;

}


.profile-label {
  width: 20%;
}

.profile-field-img {
  justify-content: center;
  margin-bottom: 40px;
  margin-top: 20px;
}

.profile-field-img > img {
  width: 130px;
}


.patient-cal-date-field {
  display: flex;
  align-items: center;  
}

hr {
  width: 100%;
  height: 1px;
  background-color: #51B8F9;
  border-color: transparent;
}

.time-planner {
  margin-top: 0px;
}

.patient-cal-day-times {
  padding: 5px;
  margin: 5px;
  border-radius: 10px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);

}

.user-type-choice {
  font-size: 10px;
}

.prescription-form{
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  background-color: white;
  width: 100%;
  padding: 0px 10px; 
  border-radius: 10px;
}

.presription-inputs {
  display: flex;
  flex-direction: column;
  margin: 5px 0px;
}

.presription-inputs > * {
  margin: 2px 0px;
}

.save-pres-btn {
  width: 60%;
}

.event-content{
  display:flex;
  justify-content: space-around;
}


.profile-page-content {
  background-color: white;
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  padding: 0 15px;
  border-radius: 20px;
}


.calendar-day {
  margin: 0 10px;
  width: 20px;
  height: 20px;
}


.appointment-details-card {
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  border-radius: 20px;
}

.top-card-app-details {
  margin-top: 10px;
  text-align: center;
}

.bottom-card-app-details {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 10px;
}

.btn-app-details {
  display: flex;
  flex-direction: column;
}

.report-upload {
  /* border: 2px solid red; */
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  padding: 5px;
}


#report-group {
  width: auto;
}

.prescrition-form-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
  border-radius: 20px;
}

.medications-added-card {
  padding: 0px;
  margin: 0px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.added-med {
  font-size: 12px;
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  border-radius: 20px;
  margin: 5px;
  padding: 5px;
  min-width: 200px;
  background-color: white;
}


.add-med-btn {
  background-color: transparent;
  border-color: transparent;
  width: 100%;
}


.booking-instr-card {
  margin-top: 40px;
  border: 2px solid red;
  border-radius: 10px;
  /* padding: 5px; */
}

.booking-instructions-text {
  padding-left: 25px;
  padding-right: 10px;
  text-align: justify;
}


/* NAVBAR */

.navbar-all {
  display: flex;
  flex-direction: column;
}

/* .nav-link:hover{
  outline: violet;
