} */


.nav-link-react {
  border: 3px solid black;
}


.navbar-logo {
  display: flex;
  justify-content: center;
}


.header-logo {
  width: 300px;
}

.navbar-menu {
  /* border: 2px solid palevioletred; */
}

.nav-link{
  background-color: #51B8F9;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin: 0 1px;
} 


